import AWS from 'aws-sdk/global'
import AWSMqtt from 'aws-mqtt'
AWS.config.region = 'us-east-1' // your region
var $ = require("jquery")

//AWS.config.credentials = new AWS.CognitoIdentityCredentials({
  // IdentityPoolId: 'us-east-1:485ef9c8-f3fc-4e18-9e7a-e2ec643cda93'
//});



var Modal = function(device){
  this.title = '';
  this.data = [];
  this.hide = function(){
    $('#modal1').removeClass('modalin');
  };
  this.show = function(){
    $('#modal1').addClass('modalin');
  };
  this.setDevice = function(device){
    this.title = `Device : ${device.name}`;
    this.data = device.data;
    //title
    $('#modal1').find('.modal-title').html(this.title);
    //
    $('#modal1').find('.modal-data').html( ` X = [ ${this.data.toString()} ]`);

  }
}

var modal1 = new Modal();

var Dispositive = function(name,topic){
  this.name = name;
  this.topic = topic;
  this.data = [];
  this.state = false;
}
var Dispositives = [];

AWS.config.credentials = new AWS.Credentials('AKIAIXFVJI3U6ZMTFCMA', 'bvP4ot0S99EOPMIYtyJN6S0cAxH7fHsn3f/410ni', null);

var client = AWSMqtt.connect({
  WebSocket: window.WebSocket,
  region: AWS.config.region,
  credentials: AWS.config.credentials,
  endpoint: 'a31akdammyod4a.iot.us-east-1.amazonaws.com', // NOTE: get this value with `aws iot describe-endpoint`
  clientId: 'mqtt-client-' + (Math.floor((Math.random() * 100000) + 1))
})

client.on('connect', function(){
  client.subscribe('MySensorPolicy');
  console.log('conected..');
  setTimeout(function(){sendTestConnections();},3000);
});

client.on('message', function(topic, message){

  console.log(topic, message);
  verifyTestConnection(topic,message);

  Dispositives.map(function(element){
    if(element.topic == topic){
      element.data = JSON.parse(message.toString()).message;
      return element;
    }
    else {
      return element;
    }
  });

/*
  var $messagesContainer = document.getElementById("messagesContainer");
  var $messageContainer = document.createElement("div");

  $messageContainer.innerHTML = JSON.parse(message.toString()).message;
  $messageContainer.className = "new-message";
  $messagesContainer.appendChild($messageContainer);
*/

});

function updateIconState(name,state){
   let $card = $(`#${name}`);
   let path = '';
   if(state)
   {
     path = 'images/wifi.svg';
   }
   else
   {
     path = 'images/wifi2.svg';
   }
   $card.find('.icon-connected').attr('src',path);
}

function verifyTestConnection(topic,message){
  for(let i=0;i<Dispositives.length;i++)
  {
    if(Dispositives[i].topic == topic)
    {
      Dispositives[i].state = true;
      updateIconState(Dispositives[i].name,true);
    }
    else {
      updateIconState(Dispositives[i].name,false);
    }
  }
}

function sendTestConnection(policy){
  let msg = {message:'001'};
  console.log(`Probando conexión policy -  ${policy} ...`);
  client.publish('MyPagePolice', JSON.stringify(msg));
}

function sendTestConnections(){
  for(let i = 0;i<Dispositives.length;i++){
    updateIconState(Dispositives[i].name,false);
    sendTestConnection(Dispositives[i].topic);
  }
}

$("#cmdMessage").click(function(){
  sendTestConnections();
})

function getDevice(name){
  for(let i=0;i<Dispositives.length;i++)
  {
    if(Dispositives[i].name==name){
     return Dispositives[i];
     break;
    }
  }
}

$("body").on("click",".card-parent",function(){

  modal1.setDevice(getDevice($(this).prop('id')));
  modal1.show();

});

$(".fa-close-modal").click(function(){
  modal1.hide();
});

$(window).ready(function(){

  //GoogleCharts.load(drawChart);
  //$('#chartData').anychart('column', [3, 1, 2]);

  Dispositives.push(new Dispositive('Dispositivo_001','MySensorPolicy'));

  for(let i=0;i<Dispositives.length;i++)
  {
    $("#dispContainer").append( `<div id="${Dispositives[i].name}" class="col s12 m6 l4 card-parent">
                                    <div class="card white modal-trigger" href="#modal1">
                                      <div class="card-content white-text center-align">
                                        <div class="row">
                                          <div class="col s12">
                                           <div class="row">
                                            <div class="col s8">
                                              <span class="card-title">${Dispositives[i].name}</span>
                                            </div>
                                            <div class="col s4">
                                              <img class="icon-connected" src="images/wifi2.svg">
                                            </div>
                                           </div>
                                          </div>
                                          <div class="col s12">
                                            <i class="fa fa-signal purple darken-4 fa-5x" aria-hidden="true"></i>
                                          </div>
                                        </div>
                                      </div>
                                    </div>
                                 </div>` );
  }

});



